.card-room-reservation{
  padding: rem(40) rem(30) rem(30) rem(30);
}

.room-reservation__header{
  display: flex;
  justify-content: space-between;
}

.room-reservation__number{
  font-family: OpenSans;
  font-size: rem(22);
  font-weight: bold;
  font-style: normal;
  line-height: rem(17);
  color: $dark100;
  &:before{
    content: "\2116";
    font-size: rem(14);
    margin-right: 5px;
    font-family: Montserrat;
  }
}
.room-reservation__type{
  font-family: Montserrat;
  font-size: rem(12);
  font-weight: bold;
  font-style: normal;
  line-height: rem(15);
  margin-left: 5px;
  text-transform: uppercase;
  color: $purple;
}

.price{
  &:after{
    content: "\20BD";
    font-family: Montserrat;
  }
  &_bold{
    font-weight: bold;
  }
}

.room-reservation__header-price-body{
  color: $dark50;
}
.room-reservation__header-price-text{
  margin-left: 5px;
  font-size: rem(12);

}
.room-reservation__price-body{
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
    color: $dark75;
}

.room-reservation__price-text{
  display: flex;
  justify-content: space-between;
  width: 77%;
}

.room-reservation__price-text_info{
  &:after{
    display: flex;
    box-sizing: border-box;
    margin-left: 5px;
    min-width: 20px;
    height: 20px;

    content: 'i';
    font-family: Montserrat;
    font-weight: bold;
    font-size: 12px;
    line-height: 14px;
    color: $dark25;

    border: 1px solid $dark25;
    border-radius: 50%;
    justify-content: center;
    align-items: center;
  }

}

.room-reservation__total{
    font-family: Opensans;
    font-size: rem(19);
    font-weight: bold;
    font-style: normal;
    line-height: rem(24);

    display: flex;

    margin-top: 30px;

    justify-content: space-between;
}
.room-reservation__total-price{
  font-family: Montserrat;
  font-weight: bold;
}

.room-reservation__total-separator{
    box-sizing: border-box;
    border-bottom: 1px dashed $dark25;
    flex: 1 1;
    margin: 5px;
}

.room-reservation-button{
  margin-top: 20px;
}


