.room-info{
  display: flex;
  align-items:center;
  border-bottom: 1px solid $dark25;
  padding: 20px 0;
  &__icon{
    width: 48px;
    height: 48px;
    min-width: 48px;
    min-height: 48px;
    fill: $purple;
    margin-right: 10px;
  }
  &__text{
    max-width: 100%;
    overflow: hidden;
    color: $dark75;
    line-height: 24px;
  }
}