.search{
  display: flex;
  height: 100%;
  margin: 0 -30px;
  padding-bottom: 70px;
  &__filters-button{
    display: none;
    justify-content:center;
    align-items:center;
    overflow: auto;
    min-height: 40px;
    @media (max-width: $tablet){
      display: flex;
    }
  }
  &__filters-text{
    font-size: 16px;
  }
  &__icon{
    display: flex;
    width: 40px;
    height: 40px;
    fill:$purple;
  }
  &__filters{
    flex:0 0 25.5%;
    padding-right:30px;
    background-color: white;
    @media (max-width: $tablet){
      position: absolute;
      top:-1000px;
      z-index: 10;
      transition:all .7s ease-out 0s;
    }
  }
  &__filter-item{
    margin-top: 30px;
  }
  &__content{
    flex:0 1 79.5%;
    height: 100%;
    padding-left:30px;
  }
  &__title{
    margin-top: 30px;
    margin-bottom: 20px;
  }
  &__rooms{
    display: grid;
    justify-content:center;
    justify-items:center;
    grid-template-columns:repeat(auto-fill,minmax(240px,1fr));
    grid-gap:12px;
  }
  &__pagination{
    margin-top: 40px;
  }
}

@media (max-width: $tablet){
  .search{
    display: flex;
    flex-direction:column;
    &__filters{
      max-width: 400px;
    }
  }
  .search__filters.active{
    position: relative;
    top: 0;
  }
}