.like-button{
  position: relative;
  color: $dark25;
  margin-top: -3px;

  &__input{
    position: absolute;
    top: 4px;
    left: 5px;
    z-index: -1;
    opacity: 0;
    margin:0;
    &:checked + .like-button__text{
      color: $purple;
      &:after{
        content: '\e903';
      }
    }
    &:checked + .like-button__text:before{
      border: 1px solid $purple;
    }
  }
  &__text{
    font-size: 10px;
    line-height: 12px;
    position: relative;
    z-index: 2;
    display: inline-block;
    box-sizing: border-box;
    min-width: 40px;
    min-height: 16px;
    text-align: center;
    padding: 0 0 0 10px;
    cursor: pointer;
    &:before{
      position: absolute;
      z-index: -1;
      top: -4px;
      left: 0;
      box-sizing: border-box;
      width: 40px;
      height: 20px;
      content: '';
      cursor: pointer;
      border: 1px solid $dark25;
      border-radius: 20px;
      background: white;
    }
    &:after{
      font-family: 'Material icons';
      font-size: 9px;
      font-weight: bold;
      position: absolute;
      top: 0;
      left: 6px;
      content: '\e904';
      cursor: pointer;
    }
  }
}